.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(18, 20, 23, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 1200;
}

.ModalContainer {
    position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 24px;
  padding: 40px;

  max-width: 320px;
  z-index: 100;
  overflow-y: auto;
  max-height: 80vh;

  border-radius: 24px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);

  max-width: 100%;
    max-height: 100%;
}

.ContentWrapper {
    position: relative;
  background-color: white;
  border-radius: 24px;

    width: 461px;
  
}

.closeIconBtn {
    position: absolute;
  color: black;

  background-color: transparent;
  border: none;
  cursor: pointer;

    top: -30px;
    right: -20px;
    width: 24px;
    height: 24px;
 
}

.closeIcon {
    display: block;
    color: #121417;

    top: -30px;
    right: -20px;
    width: 24px;
    height: 24px;
}

